/* eslint-disable no-plusplus */

import React, { useState, useRef } from 'react';
import CountDown from './Countdown';

const CountdownContainer = () => {
  const myAudio = useRef();

  const handleBeep = () => {
    if (myAudio.current !== null) {
      myAudio.current.play();
    }
    return myAudio.current.play();
  };

  const [time, setTime] = useState({
    min: 0,
    sec: 0,
  });
  const handleInputChange = event =>
    setTime({
      ...time,
      [event.currentTarget.name]: Number(event.currentTarget.value),
    });

  const { min, sec } = time;
  console.log(min, sec);
  const [interv, setInterv] = useState();
  const [keys, setKeys] = useState(0);

  let updatedS = time.sec;
  let updatedM = time.min;
  let updateMS = 0;
  let oversize = 0;
  let percentage = 100;
  const singleValue = updatedM * 60 + updatedS;
  if (updatedS > 60) {
    oversize = updatedS;
    updatedM += Math.floor(oversize / 60);
    updatedS -= Math.floor(oversize / 60) * 60;
  }
  if (updatedM > 720) {
    updatedM = 720;
  }
  if (oversize === 0) {
    oversize = updatedS;
  }
  if (percentage === updatedM * 60 + updatedS) {
    percentage = 100;
  } else {
    percentage = (singleValue / (updatedM * 60 + updatedS)) * 100;
  }

  const stop = () => {
    clearInterval(interv);
    setKeys(0);
  };

  const reset = () => {
    clearInterval(interv);
    setKeys(0);
    setTime({
      msec: 100,
      sec: 1,
      min: 1,
    });
  };

  const run = () => {
    if (updatedM === 0 && updatedS === 0) {
      updatedM = 0;
      updatedS = 0;
      handleBeep();
      return stop();
    }
    if (updatedS === 0) {
      updatedM--;
      updatedS = 59;
    }
    if (updateMS === 0) {
      updatedS--;
      updateMS = 99;
    }
    updateMS--;
    return setTime({
      msec: updateMS,
      sec: updatedS,
      min: updatedM,
    });
  };

  const start = () => {
    run();
    setKeys(1);
    setInterv(setInterval(run, 10));
  };

  return (
    <div>
      <CountDown
        keys={keys}
        minutes={updatedM}
        seconds={updatedS}
        reset={reset}
        stop={stop}
        start={start}
        handleInputChange={handleInputChange}
        value={time}
        percentage={percentage}
      />
      <audio src="https://avto-life.club/alert.ogg" ref={myAudio} preload="true" id="1" />
    </div>
  );
};

export default CountdownContainer;
